import * as React from "react";
import { useForm, SubmitHandler } from "react-hook-form";
import Layout from "../components/layout/Layout";
import Container from "react-bootstrap/Container";
import Form from "react-bootstrap/Form";
import Button from "react-bootstrap/Button";
import Col from "react-bootstrap/Col";
import Row from "react-bootstrap/Row";
import { Typeahead } from "react-bootstrap-typeahead";
import Parse, { runCloudFunction } from "../vendor/ParseApp";
import { getPdfText } from "../helpers/pdf";
import { countries } from "../helpers/country";
import AppContext from "../context/AppContext";

enum GenderEnum {
  female = "female",
  male = "male",
  other = "other",
}

interface IFormInput {
  firstName: String;
  gender: GenderEnum;
}

export default function App() {
  const { useEffect, useContext } = React;
  const [file, setFile] = React.useState(null);
  const [model, setModel] = React.useState({});

  const setModelValue = (name, value) => {
    setModel({
      ...model,
      [name]: value,
    });
    console.log(model);
  };

  const context = useContext(AppContext);

  useEffect(() => {
    context.showLoading(true);
    runCloudFunction("getMyProfile").then((app) => {
      if (app) {
        setModel({
          ...app.get("data"),
        });
      }

      context.showLoading(false);
    });
  }, [context.showLoading]);

  const isModelValid = () => {
    return model?.location?.length && model?.tags?.length && model.consent;
  };
  return (
    <>
      <Container className="">
        <Row>
          <Col>
            <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
              <Form.Label>Contact Number</Form.Label>

              <Row>
                <Col xs={3}>
                  <Form.Control
                    type="text"
                    placeholder="Country Code"
                    value={model?.telCode}
                    onChange={(e) => setModelValue("telCode", e.target.value)}
                  />
                </Col>
                <Col xs={9}>
                  <Form.Control
                    type="text"
                    placeholder=""
                    value={model?.contact}
                    onChange={(e) => setModelValue("contact", e.target.value)}
                  />
                </Col>
              </Row>
            </Form.Group>
          </Col>
        </Row>

        <Form>
          <Button
            variant="primary"
            onClick={async () => {
              context.showLoading(true);
              await runCloudFunction("updateProfile", {
                model: {
                  data: model,
                },
              });
              context.showLoading(false);
            }}
          >
            Update
          </Button>
        </Form>
      </Container>
    </>
  );
}

export const Head: HeadFC = () => (
  <>
    <title>Staart</title>
    <script src="/assets/pdf/pdf.js" type="text/javascript" />
  </>
);
